import React, { FC, Fragment, ReactElement, useState } from "react";
import { MdFullscreen, MdFullscreenExit, MdInfoOutline } from "react-icons/md";
import { CountDown } from "../CountDown/CountDown";
import { RoomServer } from "../../interfaces";
import RoomDetailsModal from "./RoomDetailsModal";
import { ExamRules } from "../../actions";
import moment from "moment";
// import { Link } from "react-router-dom";
type ProctoringMainTopBarProps = {
  course_title: string;
  post_title: string;
  duration: number;
  started_at: string | null;
  exam_start: boolean;
  startExamHandler: Function;
  stopExamHandler: Function;
  focus_mode: boolean;
  focusToggle: Function;
  leaveRoom: Function;
  room: RoomServer;
  active_candidate: number;
  deleteRoom: Function;
  ExamDone: Function;
  exam_rules: ExamRules;
};

const ProctoringMainTopBar: FC<ProctoringMainTopBarProps> = ({
  course_title,
  post_title,
  duration,
  started_at,
  // end_at,
  exam_start,
  startExamHandler,
  stopExamHandler,
  focus_mode,
  focusToggle,
  leaveRoom,
  room,
  active_candidate,
  deleteRoom,
  ExamDone,
  exam_rules,
}): ReactElement => {
  const [info_modal, setInfo_modal] = useState(false);
  return (
    <Fragment>
      <div
        className={`${
          focus_mode === true
            ? "border-blue-500 border-opacity-30 text-white proctoring-evaluator-navigation"
            : "bg-white rounded-t-sm  rounded-md"
        } py-2 px-3 border-b z-50 grid grid-flow-row md:grid-cols-1 lg:grid-cols-2 transition-all ease-in-out`}
      >
        <div className="flex">
          <button
            onClick={() => focusToggle()}
            className={`px-2 ${
              focus_mode === true
                ? "bg-blue-900 hover:bg-blue-500 "
                : "bg-blue-100 hover:bg-blue-500 "
            } mr-2  rounded-md  hover:text-white`}
          >
            {focus_mode === true ? (
              <Fragment>
                <MdFullscreenExit className="text-3xl mx-auto -mb-2" />
                <span className="text-xs inline-block pt-1">Un-Focus</span>
              </Fragment>
            ) : (
              <Fragment>
                <MdFullscreen className="text-3xl mx-auto -mb-2" />
                <span className="text-xs inline-block pt-1">Focus</span>
              </Fragment>
            )}
          </button>
          <div className="flex items-center">
            <div className="">
              <label
                htmlFor=""
                className={`${
                  focus_mode === true ? "text-gray-200" : ""
                } text-sm m-0`}
              >
                {post_title}
              </label>
              <h3 className="text-white- text-lg -mt-2 font-bold">
                {course_title}
              </h3>
            </div>
          </div>
        </div>
        <div className="text-red lg:pt-0 sm:pt-3 text-right md:text-left lg:text-right">
          <div className="grid grid-cols-5 gap-4 text-left">
            <div>
              {exam_start === true && (
                <>
                  <label className="text-gray-400 text-sm">Duration</label>
                  <h2 className="text-xl font-bold">{duration}M</h2>
                </>
              )}
            </div>
            <div>
              {exam_start === true && (
                <>
                  <label className="text-gray-400 text-sm">Started at:</label>
                  {exam_start === true && (
                    <h2 className="text-xl font-bold">
                      {moment(started_at).format("LT")}
                    </h2>
                  )}
                </>
              )}
            </div>

            <div className="text-3xl -mt-3 ">
              {exam_start === true &&
                started_at !== null &&
                started_at !== "" && (
                  <CountDown
                    start_time={started_at}
                    duration_min={duration}
                    timeout={() => ExamDone(true)}
                    returnString={false}
                    labels
                  />
                )}
            </div>
            <div className="flex items-center pl-2">
              <button
                onClick={() => setInfo_modal(true)}
                className="btn bg-gray-100 text-gray-900 w-full hover:bg-gray-500 hover:text-white"
                title="Room details"
              >
                {/* <MdInfoOutline className="inline-block" />  */}
                Room
              </button>
            </div>

            <div className="flex items-center">
              {exam_start === false ? (
                <button
                  onClick={() => startExamHandler()}
                  className="btn bg-green-500 text-white w-full"
                  title="Start Exam"
                >
                  Start
                </button>
              ) : (
                <button
                  onClick={() => stopExamHandler()}
                  className="btn bg-red-600 text-white w-full"
                  title="stop Exam"
                >
                  Stop
                </button>
              )}
            </div>
          </div>
        </div>
      </div>

      {info_modal && (
        <RoomDetailsModal
          close={setInfo_modal}
          active_candidate={active_candidate}
          room={room}
          exam_start={exam_start}
          leaveRoomHandler={leaveRoom}
          deleteRoomHandler={deleteRoom}
          exam_rules={exam_rules}
        />
      )}
    </Fragment>
  );
};

export default ProctoringMainTopBar;
