import React, { FC, Fragment, ReactElement, useState } from "react";
import { MdAddAlarm, MdClose } from "react-icons/md";
import { CandidateProctoringInterface } from "../../interfaces";

type AddTimeToCandidateModalProps = {
  close: Function;
  candidate: CandidateProctoringInterface | null;
  addMinutesHandler: Function;
};

const AddTimeToCandidateModal: FC<AddTimeToCandidateModalProps> = ({
  close,
  candidate,
  addMinutesHandler,
}): ReactElement => {
  const [minutes, setMinutes] = useState(1);
  const [reason, setReason] = useState("");
  const [error, setError] = useState({ target: "", msg: "" } as {
    target: string;
    msg: string;
  });

  const submitHandler = () => {
    if (minutes < 2) {
      return setError({ target: "minutes", msg: "Minimum is 2m" });
    }

    if (reason.length <= 3) {
      return setError({
        target: "reason",
        msg: "Reason must me up to 4 characters",
      });
    }
    addMinutesHandler(minutes, reason);
    close();
  };

  return (
    <Fragment>
      <div
        className="fixed bg-black bg-opacity-10 top-0 bottom-0 right-0 left-0 animate__animated animate__fadeIn"
        onClick={() => close(false)}
      ></div>
      <div className="fixed w-80 shadow-lg bg-white rounded top-16 mt-2 right-2 animate__animated animate__zoomIn animate__faster">
        <div className="flex items-center border-b">
          <div className="flex-1 pl-2 text-xl font-bold text-gray-400">
            <h4>Room details</h4>
          </div>

          <div className="">
            <button
              onClick={() => close(false)}
              className="p-2 hover:bg-red-700 hover:text-white rounded text-2xl"
            >
              <MdClose />
            </button>
          </div>
        </div>
        <div className="p-2 ">
          <div className="border-separate p-2">
            {[
              {
                title: "Names",
                value: candidate?.fname + " " + candidate?.lname,
              },
              { title: "Email", value: candidate?.email },
              { title: "Seat", value: candidate?.seat_number },
            ].map(({ title, value }, i) => (
              <div key={i} className="flex">
                <span className="text-gray-500">{title}: </span>
                <b className="text-right inline-block"> {value}</b>
              </div>
            ))}
          </div>

          <div className="p-2 text-center pt-1 bg-gray-100 mx-2 rounded">
            <p className="m-0 text-sm text-gray-500">Minutes</p>
            <div className="flex">
              <button
                className="text-3xl px-4 hover:bg-blue-200 hover:text-blue-900 rounded"
                onClick={setMinutes.bind(this, minutes - 1)}
              >
                -
              </button>
              <input
                type="number"
                className="flex-1 text-4xl text-center font-bold bg-transparent text-blue-700"
                min={1}
                max={10}
                value={minutes}
                onChange={(e) => setMinutes(+e.target.value)}
              />
              <button
                className="text-3xl px-4 hover:bg-blue-200 hover:text-blue-900 rounded"
                onClick={setMinutes.bind(this, minutes + 1)}
              >
                +
              </button>
            </div>
            {error.target === "minutes" && (
              <span className="text-red-500 text-sm">{error.msg}</span>
            )}
          </div>

          <div className="p-2">
            <label htmlFor="minutes-reason">Reason</label>
            <textarea
              id="minutes-reason"
              className={`w-full bg-gray-100 rounded border-2 ${
                error.target === "reason" ? "border-red-500" : "border-blue-500"
              } p-1`}
              rows={3}
              value={reason}
              onChange={(e) => setReason(e.target.value)}
            ></textarea>
            {error.target === "reason" && (
              <span className="text-red-500 text-sm">{error.msg}</span>
            )}
          </div>

          <div className="p-2 mx-1">
            <button
              className="btn bg-blue-500 text-white w-full hover:bg-blue-600"
              onClick={() => submitHandler()}
            >
              <MdAddAlarm className="inline-block text-2xl" /> Add minutes
            </button>
          </div>
        </div>
      </div>{" "}
    </Fragment>
  );
};
export default AddTimeToCandidateModal;
