import React, { FC, Fragment, ReactElement, useState } from "react";
import { MdClose } from "react-icons/md";
import { EventInterface, CandidateProctoringInterface } from "../../interfaces";

type ProctoringEventsProps = {
  events: EventInterface[];
  clickOpen?: boolean;
  clickHandler?: Function;
  candidates: CandidateProctoringInterface[];
  focus_mode: boolean;
  loading: boolean;
};

const ProctoringEvents: FC<ProctoringEventsProps> = ({
  events,
  clickOpen = true,
  clickHandler,
  candidates,
  focus_mode,
  loading,
}): ReactElement => {
  /* function body */
  const [selected, setSelected] = useState("");

  return (
    <div
      className={`mt-2 p-2 ${
        focus_mode === true
          ? "bg-gray-900 text-white"
          : "bg-gray-50 shadow-sm border"
      } rounded-md `}
    >
      <h1 className="text-xl">Events</h1>
      {loading === true ? (
        <div className="p-2">
          <h4 className="block animate__animated animate__fadeIn animate__infinite p-3 text-center">
            Loading events...
          </h4>
        </div>
      ) : (
        <table className="w-full text-left">
          <thead className="text-sm text-gray-500">
            <tr>
              <th>#</th>
              <th>username</th>
              <th>Event type</th>
              <th>Time</th>
            </tr>
          </thead>
          <tbody>
            {events.length === 0 ? (
              <tr>
                <td className="p-3 py-8 text-center" colSpan={4}>
                  <h2 className="font-bold text-2xl">No events found</h2>
                  <h2 className="">No event has been committed yet</h2>
                </td>
              </tr>
            ) : (
              events.map((evnt: EventInterface, i: number) => {
                let findUser = candidates.find(
                  (cand: CandidateProctoringInterface) =>
                    cand.user_id === evnt.user_id
                );

                return (
                  <Fragment key={i}>
                    {selected === evnt.event_id ? (
                      <tr>
                        <td
                          colSpan={4}
                          className="animate__animated animate__zoomIn animate__faster"
                        >
                          <div className="overflow-hidden my-1 rounded border bg-blue-900 bg-opacity-30 border-blue-500">
                            <div className="px-3  flex items-center bg-blue-900 bg-opacity-30">
                              <h2 className="flex-1 m-0 text-xl py-2 opacity-70">
                                Event <b>{i + 1}</b> details
                              </h2>
                              <button
                                onClick={() => setSelected("")}
                                className="hover:bg-red-600 text-2xl p-1.5 rounded-md"
                              >
                                <MdClose />
                              </button>
                            </div>
                            <div className="p-2">
                              <p>
                                <span className="opacity-70">Event type: </span>
                                <b>{evnt.event_type_id}</b>
                              </p>
                              <p>
                                <span className="opacity-70">Time: </span>
                                <b>{evnt.createdAt}</b>
                              </p>
                              <p className="p-1 m-1 bg-blue-200 bg-opacity-20 px-2 rounded-md">
                                <span className="opacity-70 block">
                                  Description:{" "}
                                </span>
                                <b>{evnt.description}</b>
                              </p>
                              {findUser !== undefined && (
                                <div className="p-1 mt-2 m-1 bg-blue-200 bg-opacity-20 px-2 rounded-md">
                                  <p>
                                    <span className="opacity-70">Names: </span>
                                    <b>
                                      {findUser.fname} {findUser.lname}
                                    </b>
                                  </p>
                                  <p>
                                    <span className="opacity-70">Email: </span>
                                    <b>{findUser.email}</b>
                                  </p>
                                </div>
                              )}
                            </div>
                          </div>
                        </td>
                      </tr>
                    ) : (
                      <tr
                        onClick={() => setSelected(evnt.event_id)}
                        className="p-2 rounded-md hover:bg-blue-900 bg-opacity-30 cursor-pointer"
                      >
                        <td className="px-1 opacity-95">{i + 1}</td>
                        <td className="text-blue-500 font-bold">
                          {findUser !== undefined ? (
                            <span
                              title={findUser?.fname + " " + findUser?.lname}
                            >
                              {findUser?.lname}
                            </span>
                          ) : (
                            <span className="opacity-50">System/Admin</span>
                          )}
                        </td>

                        <td className="text-red-400 text-sm pl-1">
                          {evnt.event_type_id}
                        </td>

                        <td className="text-gray-400 pl-1">2min</td>
                      </tr>
                    )}
                  </Fragment>
                );
              })
            )}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default ProctoringEvents;
