import React, { FC, ReactElement } from "react";
import { MdAccountCircle } from "react-icons/md";
import { CandidateProctoringInterface } from "../../interfaces";

type ProctoringCandidatesProps = {
  clickHandler: Function;
  candidates: CandidateProctoringInterface[];
  focus_mode: boolean;
  show_title: boolean;
  large: boolean;
};

const ProctoringCandidates: FC<ProctoringCandidatesProps> = ({
  clickHandler,
  candidates,
  focus_mode,
  show_title,
  large,
}): ReactElement => {
  /* function body */
  return (
    <div className={`mt-2 ${focus_mode === true ? "text-white p-2 " : ""}`}>
      {show_title === true && (
        <h1 className="text-3xl font-bold text-center pb-2 border-b mb-1 border-gray-100">
          Candidates
        </h1>
      )}
      <table
        className={`w-full text-left ${
          focus_mode === true ? "" : "bg-white rounded-md"
        }  p-2"`}
      >
        <thead className="text-sm text-gray-500">
          <tr>
            <th></th>
            <th></th>
            {large === true && <th></th>}
            <th></th>
          </tr>
        </thead>
        <tbody>
          {candidates.map((cand: CandidateProctoringInterface, i: number) => (
            <tr
              key={i}
              className={`group border-b mb-1 ${
                focus_mode === true
                  ? "border-blue-800 border-opacity-60 hover:bg-blue-900"
                  : "border-gray-200 border-opacity-60 hover:bg-blue-100"
              }  cursor-pointer`}
              onClick={clickHandler.bind(this, cand.user_id)}
            >
              <td
                className={`${
                  focus_mode === true ? "text-blue-500" : "text-gray-400"
                }  w-5 text-left`}
              >
                <MdAccountCircle
                  className={large === true ? "text-3xl" : "text-5xl"}
                />
              </td>

              <td className="text-gray-400 pl-1 py-1">
                <h4
                  className={`font-bold ${
                    large === true ? "text-md" : "text-lg"
                  }  p-0 m-0 ${
                    focus_mode === true ? "text-white" : "text-gray-900"
                  }`}
                >
                  {cand.fname} {cand.lname}
                </h4>
                {large === false && (
                  <p className="text-sm p-0 m-0 -mt-1">{cand.email}</p>
                )}
              </td>
              {large === true && (
                <td className="">
                  <span className="text-sm p-0 m-0 -mt-1">{cand.email}</span>
                </td>
              )}
              <td
                className={`text-right group-hover:opacity-100 opacity-40 ${
                  large === false
                    ? "grid grid-cols-2 text-center w-10 pt-2"
                    : ""
                } md:w-auto`}
              >
                {cand.joined === true && (
                  <span className="inline-block text-xs bg-blue-300 px-1 rounded-md m-0.5">
                    #Joined
                  </span>
                )}
                {cand.active === true && (
                  <span className="inline-block text-xs bg-green-300 px-1 rounded-md m-0.5">
                    #Active
                  </span>
                )}
                {cand.removed === true && (
                  <span className="inline-block text-xs bg-red-300 text-white px-1 rounded-md m-0.5">
                    #Removed
                  </span>
                )}
                {cand.done === true && (
                  <span className="inline-block text-xs bg-yellow-300 px-1 rounded-md m-0.5">
                    #Done
                  </span>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

ProctoringCandidates.defaultProps = {
  large: false,
};

export default ProctoringCandidates;
