import React, { FC, ReactElement } from "react";
import { CandidateProctoringInterface } from "../../interfaces";

type ProctoringStatisticsProps = {
  focus_mode: boolean;
  candidates: CandidateProctoringInterface[];
};

const ProctoringStatistics: FC<ProctoringStatisticsProps> = ({
  focus_mode,
  candidates,
}): ReactElement => {
  let total_candidates = candidates.length;
  let active_candidates = candidates.filter(
    (itm) => itm.active === true
  ).length;
  let done_candidates = candidates.filter((itm) => itm.done === true).length;
  let remain_candidate = candidates.filter(
    (itm) =>
      itm.done === false &&
      itm.active === true &&
      itm.stopped === false &&
      itm.removed === false
  ).length;
  let removed_candidate = candidates.filter(
    (itm) => itm.removed === true
  ).length;
  let stopped_candidate = candidates.filter(
    (itm) => itm.stopped === true && itm.done === false
  ).length;
  let left_candidates = candidates.filter(
    (itm) => itm.active === false && itm.done === false && itm.joined === true
  ).length;
  let non_joined_candidates = candidates.filter(
    (itm) => itm.joined === false
  ).length;

  /* function body */
  return (
    <div
      className={`grid grid-cols-4 gap-2 p-3 border-separate ${
        focus_mode === true
          ? "bg-blue-900 text-gray-300"
          : "bg-white  shadow-lg border "
      } rounded-md my-2 text-center w-90`}
    >
      {[
        {
          title: "Total",
          label: "Total candidates",
          value: total_candidates,
        },
        {
          title: "Active",
          label: "Active candidate(s)",
          value: active_candidates,
        },
        {
          title: "Done",
          label: "Candidate(s) who done exam",
          value: done_candidates,
        },
        {
          title: "Left",
          label: "Candidate(s) who left unfinished",
          value: left_candidates,
        },
        {
          title: "Removed",
          label: "Removed candidate(s)",
          value: removed_candidate,
        },
        {
          title: "Stopped",
          label: "Stopped candidate(s)",
          value: stopped_candidate,
        },
        {
          title: "Remain",
          label: "Remain candidate(s)",
          value: remain_candidate,
        },
        {
          title: "Non Joined",
          label: "Non joined exam(s)",
          value: non_joined_candidates,
        },
      ].map(
        (
          {
            title,
            label,
            value,
          }: { title: string; label: string; value: number },
          i
        ) => (
          <div key={i} title={label} className="border rounded-md py-1">
            <h1 className="text-2xl font-bold -mb-1 p-0 ">{value}</h1>
            <label htmlFor="" className="text-sm -mt-2">
              {title}
            </label>
          </div>
        )
      )}
    </div>
  );
};
export default ProctoringStatistics;
