import React, { FC, Fragment, ReactElement } from "react";
import { MdClose, MdDeleteForever, MdLogout } from "react-icons/md";
import { ExamRules } from "../../actions";
import { RoomServer } from "../../interfaces";

type RoomDetailsModalProps = {
  close: Function;
  active_candidate: number;
  room: RoomServer;
  exam_start: boolean;
  leaveRoomHandler: Function;
  deleteRoomHandler: Function;
  exam_rules: ExamRules;
};

const RoomDetailsModal: FC<RoomDetailsModalProps> = ({
  close,
  active_candidate,
  room,
  exam_start,
  leaveRoomHandler,
  deleteRoomHandler,
  exam_rules,
}): ReactElement => {
  return (
    <Fragment>
      <div
        className="fixed bg-black bg-opacity-10  top-0 bottom-0 right-0 left-0 animate__animated animate__fadeIn"
        onClick={() => close(false)}
      ></div>
      <div className="fixed w-80 z-50 shadow-lg bg-white rounded top-16 mt-2 right-2 animate__animated animate__zoomIn animate__faster">
        <div className="flex items-center border-b">
          <div className="flex-1 pl-2 text-xl font-bold text-gray-400">
            <h4>Room details</h4>
          </div>

          <div className="">
            <button
              onClick={() => close(false)}
              className="p-2 hover:bg-red-700 hover:text-white rounded text-2xl"
            >
              <MdClose />
            </button>
          </div>
        </div>
        <div className="p-2 ">
          <div className="border-separate p-2">
            {[
              { title: "Province", value: room.province_name },
              { title: "District", value: room.district_name },
              { title: "Center name", value: room.center_name },
              { title: "Group", value: room.room_number },
              { title: "Joined candidates", value: active_candidate },
            ].map(({ title, value }, i) => (
              <div key={i} className="flex">
                <span className="text-gray-500">{title}: </span>
                <b className="text-right inline-block"> {value}</b>
              </div>
            ))}
          </div>

          <div className="border-separate px-2 pt-2 mt-2 bg-gray-50 border rounded">
            <h2 className="text-xl font-bold border-b">Exam rules</h2>
            {[
              {
                title: "Leave the browser",
                value: exam_rules.allow_to_leave_browser,
              },
              { title: "Copy contents", value: exam_rules.copy_enabled },
              { title: "Paste contents", value: exam_rules.paste_enabled },
              {
                title: "Right click",
                value: exam_rules.right_click_enabled,
              },
              {
                title: "Refresh browser",
                value: exam_rules.stop_candidate_when_comeback,
              },
            ].map(({ title, value }, i) => (
              <div key={i} className="flex items-center border-b p-1">
                <div className="flex-1 text-sm">
                  <span className="">{title}: </span>
                </div>

                <div className="text-right inline-block">
                  {" "}
                  {value === false ? (
                    <span className="p-0.5 px-1 text-xs rounded bg-green-100 text-green-700">
                      Enabled
                    </span>
                  ) : (
                    <span className="p-0.5 px-1 text-xs rounded bg-red-200 text-red-700">
                      Disabled
                    </span>
                  )}
                </div>
              </div>
            ))}
          </div>
          <div className="p-2 flex">
            <button
              onClick={() => leaveRoomHandler()}
              className="btn bg-red-700 hover:bg-red-800 text-white w-full mr-2"
              title="Leave room"
            >
              <MdLogout className="inline-block" /> Leave
            </button>
            {exam_start === false && active_candidate <= 1 && (
              <button
                onClick={() => deleteRoomHandler()}
                className="btn bg-gray-500 hover:bg-red-500 text-white w-full"
                title="Delete room"
              >
                <MdDeleteForever className="inline-block" /> Delete
              </button>
            )}
          </div>
        </div>
      </div>{" "}
    </Fragment>
  );
};
export default RoomDetailsModal;
